import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PharmaService } from '../pharma.service';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.css']
})
export class PaymentComponent implements OnInit {

  public billId;
  public billData;
  public paymemntHistory;

  constructor(private route:ActivatedRoute,private pharma:PharmaService) {
   }

  ngOnInit() {
    this.route.params.subscribe((data)=>{
      this.billId = data.id
    })
    this.getBillData()
    this.getPaymentHistory()
  }

  getBillData(){
    this.pharma.getSingleBillData(this.billId).subscribe((data)=>{
      this.billData = data
    })
  }

  getPaymentHistory(){
    this.pharma.getPaymentByBillId(this.billId).subscribe((data)=>{
      this.paymemntHistory = data;
    })
  }

  addAmount(paidAmount){
  
    let paymentObj = ({
        billId:this.billId,
        paidAmount:paidAmount
    })
    this.pharma.addPayment(paymentObj).subscribe(data => {
      let newPayment =  'newPayment';
      this.paymemntHistory.push(data[newPayment])
    }, error => {
      console.log(error)
    })
  }



}
